.Slideshow_Slideshow__1JYaM { 
    position: fixed;
    width: 100%;
    min-height: 100vh;
    border-top: 2px solid #fff;
    top: 80px;
    left: 0;
    z-index: 0;
    list-style: none;
    margin: 0;
    padding: 0;
}

.Slideshow_Slideshow__1JYaM li { 
    width: 100%;
    min-height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    background-size: cover;
    background-position: 50% 50%;
    background-repeat: no-repeat;
    opacity: 0;
    z-index: 0;
    animation: Slideshow_imageAnimation__3eSMA 60s linear infinite; 
}

