    animation-delay: 10s; 
}
.Slideshow_Slideshow__1JYaM li:nth-child(3) { 
    background-image: url(/etara-web/static/media/3.56248f2e.jpg);
    animation-delay: 20s; 
}
.Slideshow_Slideshow__1JYaM li:nth-child(4) { 
    background-image: url(/etara-web/static/media/4.251c079f.jpg);
    animation-delay: 30s; 
}
.Slideshow_Slideshow__1JYaM li:nth-child(5) { 
    background-image: url(/etara-web/static/media/5.90941411.jpg);
    animation-delay: 40s; 
}
.Slideshow_Slideshow__1JYaM li:nth-child(6) { 
    background-image: url(/etara-web/static/media/6.7e2b64f9.jpg);
    animation-delay: 50s; 
}

@keyframes Slideshow_imageAnimation__3eSMA { 
0% { 
    opacity: 0; 
    animation-timing-function: ease-in;
}
10% {
    opacity: 1;
    animation-timing-function: ease-out;
}
20% {
    opacity: 1
}
30% {
    opacity: 0
}
